@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --header-height: 3rem;

  --hue: 0;
  --sat: 0%;
  --title-color: hsl(var(--hue), var(--sat), 20%);
  --title-color-dark: hsl(var(--hue), var(--sat), 0%);
  --text-color: hsl(var(--hue), var(--sat), 46%);
  --body-color: hsl(var(--hue), var(--sat), 98%);
  --container-color: #fff;
  --body-font: "Poppins", san-serif;

  --big-font-size: 3.5rem;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  --font-normal: 400;
  --font-medium: 500; 
  --font-semi-bold: 600;

  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

  /* responsive typography */
  @media screen and (max-width: 992px) {
    :root {

      --big-font-size: 2.75rem;
      --h1-font-size: 1.5rem;
      --h2-font-size: 1.25rem;
      --h3-font-size: 1rem;
      --normal-font-size: 0.938rem;
      --small-font-size: 0.813rem;
      --smaller-font-size: 0.75rem;
    }
  }

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box; 
  }

  html {
    scroll-behavior: smooth;
  }

  body, 
  button, 
  input, 
  textarea {
    font-family: "Poppins", san-serif;
    font-family: var(--body-font);
    font-size: 1rem;
    font-size: var(--normal-font-size);
  }

  body{
    background-color: hsl(0, 0%, 98%);
    background-color: var(--body-color);
    color: black;
  }

  h1, h2, h3{
     -webkit-columns: hsl(0, 0%, 20%);
             columns: hsl(0, 0%, 20%);
     -webkit-columns: var(--title-color);
             columns: var(--title-color);
     font-weight: 600;
     font-weight: var(--font-semi-bold);
  }

  ul {
    list-style: none;
  }

  a {
    text-decoration: none;
  }

  button {
    cursor: pointer;
    border: none;
    outline: none;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  .section {
    padding: 6rem 0 2rem;
  }

  .section__title{
    font-size: 2.25rem;
    font-size: var(--h1-font-size);
    color: hsl(0, 0%, 20%);
    color: var(--title-color);
    text-align: center;
  }

  .section__subtitle{
    display: block;
    font-size: 0.875rem;
    font-size: var(--small-font-size);
    margin-bottom: 4rem;
    text-align: center;
  }

  .section__title
  .section__subtitle {
    text-align: center;
  }

   /* layout  */
   .container {
    max-width: 968px;
    margin-left: auto;
    margin-right: auto;
   }

   .grid{
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem;
   }

   /* buttons */
   .button {
    display: inline-block;
    background-color: black;
    color: #fff;
    color: var(--container-color);
    padding: 1.25rem 2rem;
    border-radius: 1rem;
    font-weight: 500;
    font-weight: var(--font-medium);
   }

   .button:hover {
    background-color: rgba(0, 0, 0, 0.85);

   }

   .button__icon {
    margin-left: 0.5rem;
    margin-left: var(--mb-0-5);
   }

   .button--flex {
    display: inline-flex;
    align-items: center;
   }


   /* large devices */
   @media screen and (max-width: 992px){
      .container {
        margin-left: 2rem;
        margin-left: var(--mb-2);
        margin-right: 2rem;
        margin-right: var(--mb-2);
      }

      .button {
        padding: 1rem 1.75rem;
      }

      .button__icon{
        width: 22px;
        height: 22px;
      }

   }

   /* medium devices */
   @media screen and (max-width: 768px){
    body {
      margin: 0 0 3rem 0;
      margin: 0 0 var(--header-height) 0;
    }

    .section {
      padding: 2rem 0 4rem;
    }

    .section__subtitle {
      margin-bottom: 3rem;
      margin-bottom: var(--mb-3);
    }
   }

   @media screen and (max-width: 576px){

   }

   /* small devices */
   @media screen and (max-width: 350px){
     :root {
      --big-font-size: 2.25rem;
     }

    .container {
      margin-left: 1rem;
      margin-left: var(--mb-1);
      margin-right: 1rem;
      margin-right: var(--mb-1);
    }

   }

.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: rgb(253, 253, 253)
    
}

.nav {
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;

}

.nav__logo,
.nav__toggle {
    color: black;
    font-weight: var(--font-medium);
}

.nav__list {
    display: flex;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    

}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;
    
}

.nav__icon,
.nav__close,
.nav__toggle {
    display: none;
}

/* Active Link */

.active-link,
.nav__link:hover {
    color: green;
}

/* Change background header */
.scroll-header {
    box-shadow: 0 -1px rgba(0, 0, 0, 0.15);
}

/* medium devices */
@media screen and (max-width: 768px){
    .header{
        top: initial;
        bottom: 0;
    }

    .nav {
        height: var(--header-height);

    }

    .nav__menu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: white;
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;

    }

    /* Show Menu */

    .show-menu{
        bottom: 0;
    }

    .nav__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 2rem;
        gap: 2rem;
    }

    .nav__icon {
        font-size: 1.2rem;
    }

    .nav__close {
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--title-color);
    }

    .nav__close:hover {
        color: var(--title-color-dark);
    }

    .nav__toggle{
        font-size: 1.1rem;
        cursor: pointer;
    }

    .nav__icon,
    .nav__close,
    .nav__toggle {
        display: block;
    }

}

/* small devices */

@media screen and (max-width: 350px){
    .nav__menu{
        padding: 2rem 0.25rem 4rem;
    }

    .nav__list{
        grid-column-gap: 0;
        -webkit-column-gap: 0;
                column-gap: 0;
    }



}
.home__container{
    grid-row-gap: 7rem;
    row-gap: 7rem;
}

.home__content{
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5.5rem;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    align-items: center;
}

.home__social{
    display: grid;
    grid-template-columns: -webkit-max-content;
    grid-template-columns: max-content;
    grid-row-gap: 1rem;
    row-gap: 1rem;

}

.home__social-icon{
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover{
    color: var(--title-color-dark);
}

.home__title {
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}

.home__hand {
    width: 38px;
    height: 38px;
    margin-left: 0.9rem;
}

.home__subtitle {
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 5.4rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
}

.home__subtitle::before{
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: black;
    left: 0;
    top: 1rem;
}

.home__description {
    max-width: 400px;
    margin-bottom: var(--mb-3);
}

.home__img{
    background: url(/website/static/media/profile.ac4e5bfe.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgba(255 255 255 / 30%);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    -webkit-animation: profile__animate 8s ease-in-out infinite 1s;
            animation: profile__animate 8s ease-in-out infinite 1s;

}

@-webkit-keyframes profile__animate{
    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

}

@keyframes profile__animate{
    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

}

.home__scroll {
    margin-left: 9.25rem;
}

.wheel {
    -webkit-animation: scroll 2s ease infinite;
            animation: scroll 2s ease infinite;
}

@-webkit-keyframes scroll{
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    30% {
        -webkit-transform: translateY(3.75rem);
                transform: translateY(3.75rem);
    }
}

@keyframes scroll{
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    30% {
        -webkit-transform: translateY(3.75rem);
                transform: translateY(3.75rem);
    }
}

.home__scroll-name{
    color: black;
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;

}

.home__scroll-arrow{
    font-size: 1.25rem;
    color: black;

}

/* large devices */
@media screen and (max-width: 992px){
    .home__content{
